import Vue from "vue";
import VueRouter from "vue-router";
import Login from "@/views/Login";
import Home from "@/views/Home";
import Error from "@/views/Error";

// hack router push callback
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject)
    return originalPush.call(this, location, onResolve, onReject);
  return originalPush.call(this, location).catch((err) => err);
};

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    meta: {
      keepAlive: true,
    },
    component: Home,
  },
  {
    path: "/login",
    name: "Login",
    meta: {
      keepAlive: true,
    },
    component: Login,
  },
  {
    path: "/error",
    name: "Error",
    meta: {
      keepAlive: false,
    },
    component: Error,
  },
  {
    path: "/leavemsg-list",
    name: "LeaveMsgList",
    meta: {
      keepAlive: false,
    },
    component: () =>
      import(
        /* webpackChunkName: "group-foo" */ "@/views/LeaveMsg/leavemsg-list.vue"
      ),
  },
  {
    path: "/leavemsg-detail",
    name: "LeaveMsgDetail",
    meta: {
      keepAlive: false,
    },
    component: () =>
      import(
        /* webpackChunkName: "group-foo" */ "@/views/LeaveMsg/leavemsg-detail.vue"
      ),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
